* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, h1, h2, button {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

:root {
  --primary-color: #4a76d0;
  --text-color: #fff;
  --background-color: #1a1a1a;
  --accent-color: #e44d3a;
  --star-color-default: #bbb;
  --star-color-selected: #ffd740;
  --dialog-background-color: #333;
  --overlay-background: rgba(0, 0, 0, 0.75);
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 20px;
  font-size: 16px;
  line-height: 1.6;
  height: 100vh;
  display: flex;
  align-items: center; 
  justify-content: center; 
}

.ratingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 48px;
}

.star {
  color: var(--star-color-default);
  cursor: pointer;
  transition: color 0.2s ease-in-out, transform 0.2s;
  transform: scale(2);
}

.star.selected {
  color: var(--star-color-selected);
}

.star:hover {
  transform: scale(2.1);
}

.ratingTitle {
  font-size: 24px;
  font-weight: bold;
  color: var(--primary-color);
  margin-top: 10px;
}

.dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  background-color: var(--dialog-background-color);
  border: 1px solid #444;
  box-shadow: 0 4px 6px rgba(0,0,0,0.3);
  z-index: 1001;
  width: 50%;
  max-width: 600px;
  border-radius: 8px;
  text-align: center;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--overlay-background);
  z-index: 1000;
  display: none;
}

.dialog-active .overlay {
  display: block;
}

.closeButton {
  cursor: pointer;
  background: var(--accent-color);
  border: none;
  color: var(--text-color);
  font-size: 18px;
  padding: 12px 24px;
  border-radius: 4px;
  transition: background 0.2s, transform 0.2s;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  position: fixed;
  bottom: 10px;
  left: 10px;
}

.closeButton:hover {
  background: #d11a1a;
  transform: scale(1.1);
}

.showDialogButton {
  cursor: pointer;
  background: var(--primary-color);
  border: none;
  color: var(--text-color);
  font-size: 18px;
  padding: 15px 30px;
  border-radius: 8px;
  transition: background 0.3s, transform 0.2s;
  box-shadow: 0 3px 6px rgba(0,0,0,0.3);
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1050;
}

.showDialogButton:hover {
  background: #365a96;
  transform: translateY(-3px);
}
